<template>
  <div>
    <Suspense>
      <template #default>
        <SideLastNews :menu="menuInfo" />
      </template>
      <template #fallback>
        <LoaderComponentTwo/>
      </template>
    </Suspense>
  </div>
</template>

<script>
import{reactive,onMounted} from "vue";
import EndPoints from "@/service/EndPoints";
import{defineAsyncComponent} from "vue";
import {handleError} from "@/components/composable/handleError";
const LoaderComponentTwo=defineAsyncComponent(()=>import('@/components/composable/LoaderComponentTwo'))
const SideLastNews=defineAsyncComponent(()=>import('@/components/main/LastNews/SideLastNews'))
export default {
  name: "ExternalNews",
  components:{LoaderComponentTwo, SideLastNews},
  setup(){
    const menuInfo=reactive([])
    const result=onMounted(async()=>{
      await EndPoints.getExternalNews()
      .then((response)=>{
        if(response.data.success){
          menuInfo.push(response.data.data)
        }
      })
      .catch((error)=>{
       handleError(error)
      })
    })
    return{
      menuInfo,
      result,
    }
  }
}
</script>

<style scoped>
#message{
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: pre-line;
  line-height: normal;
}
</style>